// @file
// Alignment utility classes

// Align items
.u-align-left {
  @include breakpoint(gesso-breakpoint(tablet)) {
    float: left;
    margin-right: rem(gesso-get-map(gutter-width));
  }
}

.u-align-right {
  @include breakpoint(gesso-breakpoint(tablet)) {
    float: right;
    margin-left: rem(gesso-get-map(gutter-width));
  }
}

// Clear floats
.u-clear-both,
.clear-both {
  clear: both;
}

.u-clear-left,
.clear-left {
  clear: left;
}

.u-clear-right,
.clear-right {
  clear: right;
}
