// @file
// Styles for New content.

$languages: (
  'es': 'Nuevo',
  'ar': '',
  'zh_CN': '',
  'zh_TW': '',
  'fr': 'Nouveau',
  'ht': 'Nouvo',
  'it': ' Nuovo',
  'ko': '',
  'pt': 'Novo',
  'ru': '',
  'tl': 'Bago',
  'vi': 'Mi',
);

.new::before {
  @extend .usa-tag;
  @extend .usa-tag--accent;
  content: 'New';
  display: inline-block;
  margin-right: rem(units('05')) !important;

  @each $code, $text in $languages {
    article[lang='#{$code}'] & {
      content: $text;
    }
  }
}
